//
//  interview
//

#interview {
	overflow: hidden;
  	position: relative;
	@extend %full-screen;
	.interview {
		position: absolute;
		top:0;right:0;bottom:0;left:0;
		width:100%;
		height:100%;
		background-repeat: no-repeat;
		background-size: cover;
		background-position: 50% 50%;
		// opacity: 0;
		transform: translate(0, 10px);
	    transition: all .4s;
	    z-index:5;
	    .wrapper {
	    	margin:rem(100px) auto;
	    	.counter {
				@extend %eng;
	    		width: rem(400px);
	    		color: $text-color-lgray;
	    		text-align: center;
	    		display:block;
	    		margin:rem(50px) 0;
	    		.activeNum {
	    			color: #fff;
	    		}
	    	}
		    p {
		    	color: #ccc;
		    	font-size: rem(16px);
		    	margin-bottom: rem(30px);
		    	.bold {
		    		font-weight:500;
		    		color: $text-color-white;
		    	}
		    }
		    .nav {
		    	display:inline-block;
		    	font-size: rem(20px);
		    	font-weight: 700;
		    	a {
		    		color: #fefefe;
		    		margin: 0 rem(25px);
		    		opacity: .3;
			    	&.active {
			    		color: $text-color-orange;
			    		opacity: 1;
			    		z-index: 10;
			    	}
			    }
		    }
		    .section-header {
		    	height: rem(200px);
		    	padding: rem(20px) 0 0;
		    	h4.heading {
		    		font-weight: 700;
		    		font-size: rem(40px);
		    		line-height: rem(52px);
		    	}
		    }
		    .interview-slider {
		    	width: rem(400px);
		    	.item {
		    		border-radius: 50%;
		    		width: rem(400px);
		    		height: rem(400px);
		    		background: #fff;
		    	}
		    }
	    }
	    .info- {
	    	&graphic {
	    		position:relative;
	    		width:100%;
	    		height:100%;
	    		.bar84 {
	    			position:relative;
					top:0;
					left:10%;
	    			#bar84 {
	    				display:inline-block;
	    			}
	    			.numTxt {
	    				position:relative;
						color: $text-color-white;
						font-size: rem(28px);
						left:25%;
						font-weight: 700;
	    			}
    				.text {
	    				color: $text-color-orange;
	    				margin-left:rem(140px);
	    			}
	    		}
				.bar21 {
	    			position:relative;
    				top:10%;
					left:10%;
	    			#bar21 {
	    			}
					.numTxt {
						position:relative;
	    				font-size:rem(26px);
	    				color: $text-color-muted;
	    				left:19%;
	    			}
	    			.text {
	    				color: $text-color-gray;
	    				margin-left:rem(80px);
	    				font-size: rem(18px);
	    				font-weight: 700;
	    			}
	    		}
	    		.people {
	    			position:absolute;
	    			svg {
	    				position:relative;
	    			}
	    			span {
	    				display:block;
	    				text-align:center;
	    			}
	    			&.people2013 {
	    				color: $text-color-lgray;
						top:11%;
		    			left:15%;
	    			}	
	    			&.people2030 {
	    				color: $text-color-gray;
	    				top:6%;
		    			left:33%;
	    			}	
	    			&.people2050 {
	    				color: $text-color-muted;
	    				top: -1%;
		    			left: 55%;
	    			}
	    		}
	    		svg {
		    		position:absolute;
		    		&#item1-pi {
					    left: 50%;
					    top: 5%;
					    margin: 0 0 0 -85px;
		    		}
		    		&#item1-lines {
		    			left:58%;
		    			top:10%;
		    		}
		    		&#p2013 {
			
		    		}
		    		&#p2030 {
		    			

		    		}
		    		&#p2050 {
		    			
		    		}
	    		}	
	    	}
	    	&title {
		    	font-size: rem(18px);
		    	color: $text-color-muted;
		    	text-align: center;
		    	font-weight:700;
		    	line-height:rem(26px);
		    	padding: rem(50px) 0  rem(15px);
		    }
	    	&sub {
		    	font-size: rem(15px);
		    	color: $text-color-gray;
		    	width: rem(320px);
		    }
	    	&ref {
	    		color: $text-color-lgray;
	    		text-align:center;
	    	}
	    }
		&.interview1 {
			background-image: url('../img/interview/bg-interview-1.jpg');
			.interview-slider {
		    	.owl-item:nth-child(1) {
		    		.item {
			    		background: url('../img/interview/bg-interview-1-1.jpg') no-repeat 50% 50%/cover;
			    	}	
			    }
		    	.owl-item:nth-child(2) {
		    		.item {
			    		background: url('../img/interview/bg-interview-1-2.jpg') no-repeat 50% 50%/cover;
			    	}	
			    }
		    }
		}
		&.interview2 {
			background-image: url('../img/interview/bg-interview-2.jpg');
			.interview-slider {
		    	.owl-item:nth-child(1) {
		    		.item {
			    		background: url('../img/interview/bg-interview-2-1.jpg') no-repeat 50% 50%/cover;
		    	}	}
		    	.owl-item:nth-child(2) {
		    		.item {
			    		background: url('../img/interview/bg-interview-2-2.jpg') no-repeat 50% 50%/cover;
		    	}	}
		    }
		}
		&.interview3 {
			background-image: url('../img/interview/bg-interview-3.jpg');
			.interview-slider {
		    	.owl-item:nth-child(1) {
		    		.item {
			    		background: url('../img/interview/bg-interview-3-1.jpg') no-repeat 50% 50%/cover;
		    	}	}
		    	.owl-item:nth-child(2) {
		    		.item {
			    		background: url('../img/interview/bg-interview-3-2.jpg') no-repeat 50% 50%/cover;
		    	}	}
		    }
		}
		&.interview4 {
			background-image: url('../img/interview/bg-interview-4.jpg');
			.interview-slider {
		    	.owl-item:nth-child(1) {
		    		.item {
			    		background: url('../img/interview/bg-interview-4-1.jpg') no-repeat 50% 50%/cover;
		    		}
		    	}	
		    	.owl-item:nth-child(2) {
		    		.item {
			    		background: url('../img/interview/bg-interview-4-2.jpg') no-repeat 50% 50%/cover;
		    		}
		    	}	
		    }
		}
		&.active {
        	opacity: 1;
        	transform: translate(0, 0);
        	z-index: 10;
		}
	}

}
